import { HomeScreen } from "./screens/home_screen";
import { AboutScreen } from "./screens/about_screen";
import { ContactScreen } from "./screens/contact_screen";
import { SettingScreen } from "./screens/setting_screen";
import { SampleForm } from "./screens/sample_form";
import { SampleDatatable } from "./screens/sample_datatable";
import { LoginScreen } from "./screens/login_screen";

export var appRoutes = [
    {
        title: 'Home',
        path: '/home',
        component: HomeScreen,
        isMenuItem: true,
        inApp: true
    },
    {
        title: 'About',
        path: '/about',
        component: AboutScreen,
        isMenuItem: true,
        inApp: true
    },
    {
        title: 'Contact',
        path: '/contact',
        component: ContactScreen,
        isMenuItem: true,
        inApp: true
    },
    {
        title: 'Setting',
        path: '/setting',
        component: SettingScreen,
        isMenuItem: true,
        inApp: true
    },
    {
        title: 'Sample Form',
        path: '/sample-form',
        component: SampleForm,
        isMenuItem: true,
        inApp: true
    },
    {
        title: 'Sample Datatable',
        path: '/sample-dt',
        component: SampleDatatable,
        isMenuItem: true,
        inApp: true
    },
    {
        title: 'Login Sample',
        path: '/',
        component: LoginScreen,
        isMenuItem: false,
        inApp: false
    }
]


