import React from 'react';
import { Page } from "../components/page";
import { MyCheckbox } from '../components/my_checkbox';
import { LinkedComponent } from "valuelink";

export class SampleForm extends LinkedComponent {

    constructor(props) {
        super(props);
        this.addData = this.addData.bind(this);
        this.state = {
            gender: 'male'
        }
    }

    addData(e) {
        e.preventDefault();
    }

    render() {
        return (
            <Page header='Sample Form'>
                <div className='row'>
                    <div className='col-md-3'></div>
                    <div className='col-md-6'>

                        <div className='my-card' >
                            <form action="#" onSubmit={this.addData} >
                                Name:
                                <input type="text" className="my-input" placeholder='Enter your name' required />
                                <br />
                                Address:
                                <textarea className='my-input' id="" cols="30" rows="5" placeholder='Enter your address' required ></textarea>
                                <br />
                                Email:
                                <input type="email" className="my-input" placeholder='Enter your email' required />
                                <br />
                                Mobile number:
                                <input type="number" maxLength={10} minLength={10} className="my-input" placeholder='Enter your moile number' required />
                                <br />
                                Gender:
                                <div className='row'>
                                    <div className='col-md-6'>
                                        <MyCheckbox label='Male' checked={this.state.gender === 'male'}
                                            onClick={() => this.linkAt('gender').set('male')} />
                                    </div>
                                    <div className='col-md-6'>
                                        <MyCheckbox label='Female' checked={this.state.gender === 'female'}
                                            onClick={() => this.linkAt('gender').set('female')} />
                                    </div>
                                </div>
                                <br />
                                <button className='my-btn' type='submit' >Save Data</button>
                            </form>
                        </div>

                    </div>
                    <div className='col-md-3'></div>
                </div>

            </Page>
        );
    }
}


