import React from 'react';
import { Page } from '../components/page';
import { LinkedComponent } from "valuelink";
import swal from "sweetalert";

export class LoginScreen extends LinkedComponent {

    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: ''
        }
    }

    doLogin = (e) => {
        e.preventDefault();
        if (!(this.state.username.toLowerCase() === 'arnold' && this.state.password.toLowerCase() === 'parge')) {
            swal('Incorrect username or password', '', 'warning');
        }
        else {
            this.props.history.push('/home');
        }
    }

    render() {
        return (
            <Page header="Arnold's Template Login">
                <div className='row'>
                    <div className='col-md-4'></div>
                    <div className='col-md-4'>
                        <form style={{
                            padding: 10
                        }} action="#" onSubmit={this.doLogin} >
                            Username:
                            <input type="text" {...this.linkAt('username').props} className="my-input" placeholder='Username' required />
                            <br />
                            Password:
                            <input type="password" {...this.linkAt('password').props} className="my-input" placeholder='Password' required />
                            <br />
                            <button className='my-btn' type='submit'>Login</button>
                        </form>
                    </div>
                    <div className='col-md-4'></div>
                </div>
            </Page>
        );
    }
}


