var styles = {
  bmBurgerButton: {
    position: 'fixed',
    width: '30px',
    height: '20px',
    left: '20px',
    top: '20px'
  },
  bmBurgerBars: {
    background: '#373a47'
  },
  bmCrossButton: {
    height: '24px',
    width: '24px'
  },
  bmCross: {
    background: '#bdc3c7'
  },
  bmMenu: {
    background: '#373a47',
    padding: '2.5em 1.5em 0',
    fontSize: '1.15em',
    overflow: 'hidden'
  },
  bmMorphShape: {
    fill: '#373a47'
  },
  bmItemList: {
    color: '#b8b7ad',
    padding: '0.8em'
  },
  bmItem: {
    // display: 'inline-block'
    display: 'block',
    outline: 'none',
    color: 'darkgrey',
    border: 'white 1px solid',
    padding: 10,
    textDecoration: 'none',
    borderRadius: 3,
    marginBottom: 10,
    transition: 'all 0.3s ease-in-out'
  },
  bmOverlay: {
    background: 'rgba(0, 0, 0, 0.3)'
  }
}

export {
  styles
}


