import React from 'react';
import { Page } from '../components/page';
import { MyCheckbox } from '../components/my_checkbox';

export class SettingScreen extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            checked: true
        }
    }

    render() {
        return (
            <Page header='Settings' >
                <MyCheckbox onClick={() => {
                    this.setState({
                        checked: !this.state.checked
                    });
                }} checked={this.state.checked} label='Check me out' />
            </Page>
        );
    }
}


