import React from 'react';

export class Page extends React.Component {
    render() {
        return (
            <div>
                <center style={{
                    fontSize: 40,
                    paddingTop: 30,
                    background: 'linear-gradient(grey, white)'
                }} >
                    {this.props.header}
                </center>
                <hr />
                {this.props.children}
            </div>
        );
    }
}


