import React from 'react';

export class MyCheckbox extends React.Component {
    render() {
        return (
            <div className='my-checkbox' onClick={this.props.onClick}>
                <div hidden={this.props.checked}>&nbsp;</div>
                <img hidden={!this.props.checked}
                    className='my-checkbox-img'
                    src="https://pkief.gallerycdn.vsassets.io/extensions/pkief/markdown-checkbox/1.3.0/1524392563369/Microsoft.VisualStudio.Services.Icons.Default" alt="" />
                <p>{this.props.label}</p>
            </div>
        );
    }
}


