import React, { Component } from 'react';
import { scaleRotate as Menu } from "react-burger-menu";
import { styles } from "./styles/menu";
import { Route, Link, withRouter, Switch } from "react-router-dom";
import { appRoutes } from "./routes";
import './styles/menu';
import 'bootstrap/dist/css/bootstrap.css';
import 'react-table/react-table.css';
import { myEmitter } from "./common";

class App extends Component {

  constructor(props) {
    super(props);
    myEmitter.on('toggleLoader', () => {
      this.setState({
        loading: !this.state.loading
      });
    });

  }

  componentWillUnmount() {
    myEmitter.removeAllListeners();
  }

  state = {
    isMenuOpen: false,
    loading: false
  }

  toggleMenu = () => {
    this.setState({
      isMenuOpen: !this.state.isMenuOpen
    });
  }

  onMenuStateChange = (st) => {
    this.setState({
      isMenuOpen: st.isOpen
    });
  }

  outPageList = [
    '/'
  ]

  render() {
    if (this.outPageList.indexOf(this.props.location.pathname) < 0)
      return (
        <Switch location={this.props.location} >
          <div id="outer-container">
            <Menu isOpen={this.state.isMenuOpen} onStateChange={this.onMenuStateChange} styles={styles} pageWrapId={"page-wrap"} outerContainerId={"outer-container"}>
              {appRoutes.map((rt, index) => {
                if (rt.inApp)
                  return rt.isMenuItem ? <Link to={rt.path} className='my-menu' onClick={this.toggleMenu} key={index} > {rt.title} </Link> : ''
                else return ''
              })}
              <Link onClick={() => {
                this.toggleMenu();
              }} to='/'>Logout</Link>
            </Menu>
            <main id="page-wrap">
              <div style={{
                minHeight: 750
              }} >
                {appRoutes.map((rt, index) => {
                  if (rt.inApp)
                    return <Route exact={rt.path === '/'} path={rt.path} component={rt.component} key={index} />
                  else return ''
                })}
              </div>
            </main>
            <div hidden={!this.state.loading} className='my-loader'>
              <div>
                <img height={100} width={100} src="http://superstorefinder.net/support/wp-content/uploads/2018/01/blue_loading.gif" alt="" />
                <p>Loading...</p>
              </div>
            </div>
          </div>
        </Switch>
      );
    else
      return (
        <Switch location={this.props.location} >
          <div id="outer-container">
            {appRoutes.map((rt, index) => {
              if (!rt.inApp)
                return <Route exact={rt.path === '/'} path={rt.path} component={rt.component} key={index} />
              else return ''
            })}
            <div hidden={!this.state.loading} className='my-loader'>
              <div>
                <img height={100} width={100} src="http://superstorefinder.net/support/wp-content/uploads/2018/01/blue_loading.gif" alt="" />
                <p>Loading...</p>
              </div>
            </div>
          </div>
        </Switch>
      );
  }
}

export default withRouter(App);



