import React from 'react';
import ReactTable from "react-table";
import { Page } from "../components/page";
import { LinkedComponent } from "valuelink";
import moment from 'moment';
import { myEmitter } from "../common";

export class SampleDatatable extends LinkedComponent {

    constructor(props) {
        super(props);
        this.state = {
            users: []
        }
        this.populateUsers = this.populateUsers.bind(this);
        myEmitter.emit('toggleLoader');
        this.populateUsers();
    }

    populateUsers() {
        fetch('https://randomuser.me/api?results=100').then(res => res.json()).then(res => {
            console.log('res.results: ', res.results);
            this.linkAt('users').set(res.results);
            myEmitter.emit('toggleLoader');
        });
    }

    render() {
        return (
            <Page header='Sample Datatable'>
                <div className='row'>
                    <div className='col-md-3'></div>
                    <div className='col-md-6'>
                        <ReactTable
                            data={this.state.users}
                            columns={[
                                {
                                    Header: 'Image',
                                    accessor: 'picture.thumbnail',
                                    Cell: data => (
                                        <img src={data.value} alt='' />
                                    ),
                                    width: 100,
                                    style: {
                                        textAlign: 'center'
                                    },
                                    filterable: false,
                                    sortable: false
                                },
                                {
                                    Header: 'Name',
                                    accessor: 'name.first'
                                },
                                {
                                    Header: 'Born',
                                    accessor: 'dob.date',
                                    Cell: data => (
                                        <span>
                                            {moment(data.value).format('ll')}
                                        </span>
                                    )
                                },
                                {
                                    Header: 'Email',
                                    accessor: 'email',
                                    width: 250
                                }
                            ]}
                            pageSize={10}
                            filterable={true}
                            SubComponent={row => {
                                return (
                                    <div style={{
                                        padding: 20
                                    }} >
                                        <b>Address: </b> <hr/>
                                        {row.original.location.street} <br/>
                                        {row.original.location.city} <br/>
                                        {row.original.location.state} <br/>
                                        {row.original.location.postcode} <br/>
                                    </div>
                                )
                            }} />
                    </div>
                    <div className='col-md-3'></div>
                </div>
            </Page>
        );
    }
}


